@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
.alerta {
  padding: 1rem 2rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #a30202;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  margin-top: 1rem;
  width: 20rem;
}